___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-attachment: fixed;
    font-size: 2rem;
}

#pop-up-sec {
    position: absolute;
    height: 400px;
    width: 600px;
    background-color: rgb(53, 143, 218);
    top: calc((100vh - 400px) / 2);
    left: calc((100vw - 600px) / 2);
    border: 3px solid black;
    border-radius: 16px;
    display: none;

    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    
}

.mode-btn {
    font-size: inherit;
    border-width: 0;
    border-radius: 12px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: gray;
}
#logo-container {
    height: 100px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

#instruction-sec {
    position: absolute;
    top: 0;
    > button {
        height: 30px;
        > svg {
            height: 100%;
        }
    }
    > div {
        position: absolute;
        display: none;
        width: 350px;
        padding: 12px;
        background-color: white;
        border: 1px solid black;
        border-radius: 10px;
        z-index: 2;
        > h4 {
            margin: 10px;
        }
    }
}

#monitor {
    width: 600px;
    background-color: rgb(1, 117, 59);
    border-radius: 12px;
    position: relative;
    font-size: 2em;
    padding: 10px;
    text-align: center;
    left: calc((100vw - 600px) / 2);
    z-index: 1;
}
#main-sec {
    display: flex;
    gap: 80px;
    justify-content: center;
    flex-wrap: wrap;
}

.player-name {
    margin: 20px;
    text-align: center;
}
.gameboard-container {
    height: 400px;
    width: 400px;
    border: 1px solid black;
    background-color: white;
}

#button-sec {
    > button {
        font-size: 32px;
        color: wheat;
        background-color: blue;
        border-width: 0;
        border-radius: 8px;
        width: 200px;
    }

    > button:hover {
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }

    align-self: flex-end;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}
.spot {
    border: 1px solid black;
    text-align: center;
}

.ship-container {
    background-color: aquamarine;
}

.miss {
    background-color: rgb(211, 204, 204);
}

.miss::after {
    content: "O";
    color: white;
}

.hit {
    border-color: red;
    border-width: 2px;
}

.hit::after {
    content: "X";
    color: red;
}

.sunk-ship {
    background-color: red;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,yDAA8C;IAC9C,sBAAsB;IACtB,4BAA4B;IAC5B,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,YAAY;IACZ,mCAAmC;IACnC,8BAA8B;IAC9B,+BAA+B;IAC/B,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;;IAEb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;;AAEb;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,iBAAiB;IACjB,oBAAoB;IACpB,WAAW;AACf;AACA;IACI,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN;QACI,YAAY;QACZ;YACI,YAAY;QAChB;IACJ;IACA;QACI,kBAAkB;QAClB,aAAa;QACb,YAAY;QACZ,aAAa;QACb,uBAAuB;QACvB,uBAAuB;QACvB,mBAAmB;QACnB,UAAU;QACV;YACI,YAAY;QAChB;IACJ;AACJ;;AAEA;IACI,YAAY;IACZ,iCAAiC;IACjC,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;IACd,aAAa;IACb,kBAAkB;IAClB,+BAA+B;IAC/B,UAAU;AACd;AACA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,uBAAuB;AAC3B;;AAEA;IACI;QACI,eAAe;QACf,YAAY;QACZ,sBAAsB;QACtB,eAAe;QACf,kBAAkB;QAClB,YAAY;IAChB;;IAEA;QACI,4CAA4C;IAChD;;IAEA,oBAAoB;IACpB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,UAAU;AACd;;AAEA;IACI,qBAAqB;AACzB","sourcesContent":["body {\n    background-image: url(./images/background.jpg);\n    background-size: cover;\n    background-attachment: fixed;\n    font-size: 2rem;\n}\n\n#pop-up-sec {\n    position: absolute;\n    height: 400px;\n    width: 600px;\n    background-color: rgb(53, 143, 218);\n    top: calc((100vh - 400px) / 2);\n    left: calc((100vw - 600px) / 2);\n    border: 3px solid black;\n    border-radius: 16px;\n    display: none;\n\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    gap: 30px;\n    \n}\n\n.mode-btn {\n    font-size: inherit;\n    border-width: 0;\n    border-radius: 12px;\n    padding-top: 10px;\n    padding-bottom: 10px;\n    color: gray